#header {
    position: relative;
}


.header {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.header h1 {
  font-size: 4rem;
  font-weight: 800;
  line-height: 1.4;
  text-transform: uppercase;
  color: var(--color-white);
  position: relative;
  padding-bottom: 2rem;
}

.header h1 span {
  display: block;
}

h1 span:nth-child(2),
:nth-child(3) {
  font-weight: 100;
}

.header h1::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 65%;
  height: 1x;
  background: var(--color-white);
}

.header-left p {
  margin: 3rem 0;
  width: 70%;
}

.header-right img {
  width: 150%;
}


.floating-icon {
    width: 5rem;
    height: 5rem;
    background: var(--light-blue);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    cursor: pointer;
}

.floating-icon a{
    border: 1px solid #fff;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.mouse {
    transform: translateY(-25%);
    animation: bounce .8s ease infinite alternate;
}

@keyframes bounce{
   
    
    100% {
        transform: translateY(25%); 
    }
}