.features-content{
     display: flex;
     justify-content: center;
}

.features-left {
    flex: 1;
    text-align: center;

}


.features-left img{
    width: 65%;

}

.features-right {
    flex: 1;
}




.feature-icon {
    position: relative;
    margin-right: 2rem;
    padding-top: 30px;
}

.inner-icon {
    position: absolute;
    top: 65%;
    left: 6%;
    transform: translate(-50%, -50%);
}

.feature-text h3 {
    font-size: 2rem;
    font-weight: 300;
    line-height: 1.4;
    text-transform: uppercase;
    color: var(--light-blue);
    text-decoration: underline;
    padding-bottom: 10px;
    
}

.features-text p {
    padding-bottom: 10px;
}

@media screen and (max-width: 600px) {
    .features-content{
        flex-direction: column;
    }
}
